module.exports = ({ stores }) => () => {

    const toggleXray = () => {
        const { xray } = stores.options.getState();
        stores.options.setState({ xray: !xray });
    };

    const keyBindings = {
        'Ctrl+x': toggleXray
    };

    document.addEventListener('keyup', e => {
        const keys = `${e.ctrlKey ? 'Ctrl' : ''}+${e.key}`;
        const action = keyBindings[keys];
        if (action) action();
    });

};
