module.exports = ({ services, stores }) => async () => {

    const { freetext, fallback } = stores.gravatar.getState();

    const emails = freetext.split(/[\r\n,;]+/g).map(s => s.trim()).filter(s => s);

    services.gravatar.status.working();

    const promises = emails.map(async email => {
        const { name, imageBlob } = await services.gravatar.fetchNameAndImage(email, fallback);
        services.tags.insertImageBlob(name, imageBlob);
    });

    try {
        await Promise.all(promises);
        services.gravatar.status.ready();
        return true;
    } catch (err) {
        console.error(err);
        const errorMessage = 'An error occurred. Please check your connection and try again.';
        services.gravatar.status.error(errorMessage);
        return false;
    }

};
